import test from 'ava';
import add from './add';

test('Should return 0 on empty string', t => {
  t.is(add(''), 0);
});

test('Should return one number', t => {
  t.is(add('5'), 5);
});

test('Should return one number with spaces', t => {
  t.is(add('  5 '), 5);
});

test('Should return two numbers', t => {
  t.is(add('5,3'), 8);
});

test('Should return two numbers with spaces', t => {
  t.is(add('  5 ,  3 '), 8);
});

test('Should return two numbers with spaces', t => {
  t.is(add(' qwe '), 0);
});
